html, body {
  height: 100%;
}

body {
  color: #000;
  margin: 0;
  font-family: Noto Serif, serif;
}

h1 {
  padding: 20px 50px 0;
  font-size: 20px;
  line-height: 1.6;
}

a {
  white-space: normal;
}

p {
  width: 100%;
  margin-top: 0;
  padding-left: 50px;
  font-size: 14px;
  line-height: 1.6;
}

div {
  box-sizing: border-box;
}

.flex-container {
  flex-direction: row;
  display: flex;
}

.half {
  flex: 0 50%;
}

.left {
  text-align: left;
  flex: 0 50%;
  padding-left: 50px;
}

.right {
  text-align: right;
  flex: 0 100%;
  padding-right: 50px;
}

.body-link {
  color: #f26a87;
}

.press-link {
  color: #b88c03;
}

div.text {
  padding: 50px;
  font-size: 30px;
}

.poem {
  opacity: 0;
}

.pressure {
  opacity: 1;
}

.arrow {
  cursor: pointer;
  border: 1px solid #000;
  width: 50px;
  padding: 10px;
  font-size: 25px;
  position: absolute;
  top: 80px;
  left: 50px;
}

.poem {
  transition: opacity 1.5s;
  position: absolute;
  top: 100px;
}

@media only screen and (width >= 600px) {
  div.text {
    padding: 50px;
    font-size: 28px;
  }

  p {
    font-size: 24px;
  }

  h1 {
    font-size: 34px;
  }

  .left {
    flex: 0 60%;
  }
}
